<template>
  <div class="template-container" v-loading="loading">
    <div class="template-container-title">创建人群-智能拓展</div>

    <el-form ref="form" :model="form">
      <!--选择拓展的种子人群 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">1.选择拓展的种子人群</div>
        <div class="temp-item-main">
            <el-select v-model="form.seed_audience_id" :loading="selectLoading" placeholder="请选择">
                <el-option-group
                v-for="group in audienceList"
                :key="group.label"
                :label="group.label">
                    <el-option
                        v-for="item in group.options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                        :disabled="group.disabled">
                        <span style="float: left">{{ item.label }}</span>
                        <span style="float: right; color: #8492a6; font-size: 13px">{{ item.notice }}</span>
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
      </div>
      <!--选择拓展的种子人群 end -->
      <!--选择拓展的量级 begin -->
      <div class="temp-item-block">
        <div class="temp-item-title">2.选择拓展的量级</div>
        <div class="temp-item-main">

          <div class="temp-item-block" style="width:1600px;">
            <div class="block">
                <br>
                <span class="demonstration">拓展量级只支持50W的倍数(单位：万人)</span>
                <el-slider
                v-model="form.expand_user_count"
                :step="50"
                :max="5000">
                </el-slider>
            </div>
            <span>0</span>
            <span style="display: block;float: right;">5000W</span>
          </div>
            
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群名称：</label>
            <div class="temp-item-inlineblock">
              <input class="temp-long-inputtext" v-model="form.name" @input="audienceNameChange" type="text" maxlength="32" />
              <span class="temp-item-inlayremark">{{ name_size }}/32</span>
            </div>
          </div>
          <div class="temp-item-block">
            <label class="temp-item-label" for="">人群描述：</label>
            <div class="temp-item-inlineblock">
                <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="（选填）" v-model="form.description"></el-input>
            </div>
          </div>
        </div>
      </div>
      <!--选择拓展的量级 end -->
      <el-form-item>
        <el-button type="primary" @click="onSubmit">完成</el-button>
        <el-button @click="onCancel">取消</el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
export default {
  created: function() {
    if (this.account_id) {
        this.getList()
    }
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
        this.getList()
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
        loading: false,
        selectLoading: false,
        name_size : 0,
        description_size : 0,
        form: {
            type: 'LOOKALIKE',
            seed_audience_id: '',
            expand_user_count: 0,
            name: '',
            description: '',
        },
      
        audienceList: [{
            label: '',
            disabled: false,
            options: []
        }, {
            label: '以下人群暂不支持智能拓展',
            disabled: true,
            options: []
        }],
    }
  },
  methods: {
    onCancel() {
      this.$router.push('/setting/audience')
    },
    audienceNameChange(e) {
      this.name_size = this.form.name.length
    },
    onSubmit() {
        
      if(this.form.seed_audience_id == ''){
          this.$message.error('请选择拓展的种子人群')
          return false
      }
      if(this.form.expand_user_count == 0){
          this.$message.error('请选择拓展的量级')
          return false
      }
      if(this.form.name == ''){
          this.$message.error('人群名称不能为空')
          return false
      }
      var params = this.form
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/add', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.$router.push('/setting/audience')
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },

    // 获取人群列表
    getList() {
      this.selectLoading = true 
      let params = {}
      params.account_id = this.account_id
      var that = this
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/get', {
          params: params
        })
        .then(function(response) {
           that.selectLoading = false 
          var respData = response.data
          if (respData.code == 0) {
              that.formatAudienceList(respData.data)
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
            that.selectLoading = false 
            that.$message('网络繁忙，请稍后再试！')
            console.log(err)
        })
    },

    // 组合人群列表
    formatAudienceList(list) {
        this.audienceList[0]['options'] = []
        this.audienceList[1]['options'] = []
        list.forEach((val, index) => {
            if(val.type == 'LOOKALIKE'){
                this.audienceList[1]['options'].push({
                    value: val.id,
                    label: val.label,
                    notice: "拓展人群不能进行拓展"
                })
            }else if(val.user_count < 2000){
                this.audienceList[1]['options'].push({
                    value: val.id,
                    label: val.label,
                    notice: "不符合2k-3kw人数要求"
                })
            }else if(val.status != 'SUCCESS'){
                this.audienceList[1]['options'].push({
                    value: val.id,
                    label: val.label,
                    notice: "不是正常可用人群"
                })
            }else{
                val.user_count = (val.user_count + '').replace(/(?=(?!(\b))(\d{3})+$)/g, ",")  + '人'
                this.audienceList[0]['options'].push({
                    value: val.id,
                    label: val.label,
                    notice: val.user_count
                })
            }
        });
    },


  },
}
</script>
<style>
.temp-item-main .el-transfer {
    width: 1600px;
}
.temp-item-main .el-transfer-panel {
    width: 550px;
}
.conversion-box {
    border: 1px solid #e3e4e5;
    max-width: 200px;
    position: relative;
    zoom: 1;
    margin-top: 10px;
    padding-bottom: 10px;
}
.conversion-checkbox {
    margin-left: 30px;
    padding-top: 10px;
}
</style>



